<template>
    <div class="wrapper">
      <vhead class="wrapper_head"></vhead>
        <transition name="slide-fade">
            <router-view class="wrapper_content"></router-view>
        </transition>
      <vfoot></vfoot>
    </div>
</template>
<script>
import vfoot from './vfooter'
import vhead from './vhead'
export default {
    components:{
        vhead,vfoot
    },
    created(){
      
    }
}
</script>

<style scoped lang="scss">
@import '../../scss/media-queries';
@import '../../scss/variables';
  .wrapper{
    display: flex;
    flex-direction: column;
    &_content{
      width: 100%;
      padding: 20px 20px;
      padding-top: 0;
      margin-top: 20px;
      position: relative;
      @include tablet-min{
        width: calc((100% - 27rem)/1.5);
        margin-left: 30rem;
        padding: 40px 60px;
        position: relative;
      }
    }
  }

  .slide-fade-enter-active {
    transition: all .25s ease;
  }
  .slide-fade-leave-active {
    transition: all .25s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to{
    transform: translateX(10px);
    opacity: 0;
  }
</style>



