<template>
  <div class="home_warpper" v-loading="loading2">
      <article v-for="(item,index) in articleLists">
        <header>
					<router-link class="home_title" :to="{path:`/article/${item._id}`}">{{item.title}}</router-link>
					<div>
						<p class="home_creatAt">{{item.created_at}}</p>
					</div>
        </header>
				<footer>
						<router-link class="home_readMore" :to="{path:`/article/${item._id}`}">查看全文</router-link>
				</footer>
      </article>
    <footer class='loadMore' v-if='loadMoreShow'>
			<el-button type="primary" :loading="loadMoreFlag" @click='loadMore'>{{loadMoreText}}</el-button>
		</footer>
  </div>
</template>
<script>
import api from "../../api/index";
export default {
  data() {
    return {
      articleLists:[],
      page:1,
      limit:10,
			loadMoreShow:false,
			loadMoreFlag:false,
			loadMoreText:'加载更多',
			loading2:true
		}
  },
	methods: {
		loadMore() {
			this.loadMoreText = '加载中'
			this.loadMoreFlag = true
			this.page++
			this.loadData(this.page,this.limit)
		},
		loadData(page,limit) {
			api.getAllArticle({page,limit})
			.then((result) =>{
				if(result.data.code===200){
					setTimeout(() =>{
						this.loading2 = false
						this.articleLists = this.articleLists.concat(result.data.articleLists)
						if(result.data.hasNext){
							this.loadMoreShow = true
							this.loadMoreFlag =  false
							this.loadMoreText = '加载更多'
						}else{
								this.loadMoreShow = false
						}
					},500)
				}
			})
		}
	},
	mounted(){
		this.loadData(1,this.limit)
	}
}
</script>

<style scoped lang="scss">
@import '../../scss/media-queries';
@import '../../scss/variables';
.home{
	&_warpper{
		list-style:none;	
		article{
			box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 6px, rgba(0, 0, 0, 0.2) 0px 1px 4px;
			border-radius: 2px;
			background-color: rgba($c-white, 0.8);
			box-sizing: border-box;
			transition: all 0.5s ease;
			display: flex;
			flex-direction: column;
			margin: 16px 0;
			footer {
				padding: 32px 32px;
			}
			&:hover{
				box-shadow: rgba(0, 0, 0, 0.322) 0px 2px 12px, rgba(0, 0, 0, 0.459) 0px 2px 8px;
			}
		}
	}
	&_title{
		display: block;
		font-size: 2.5rem;
		font-weight: 400;
		color: #404040;
		padding: 32px 32px;
		margin: 0;
	}
	&_creatAt{
		font-family: "Comic Sans MS", curslve, sans-serif;
		font-size: 1.6rem;
		color:#7f8c8d;
		margin: 0;
		padding: 0 32px;
	}	
	&_readMore{
		font-size: 2rem;
    color:#919191;
    font-weight: 600;
	}
}
footer{
	text-align: right;
}
</style>




