<template lang="html">
  <div class="tags_wrap" v-loading.lock="loading2">
    <ul class="tags_list">
      <li v-for='(item,index) in items'>
        <a class="tags_btn" :to="`/tags/${item._id}`" @click.prevent="gets(index,item.classify)" :class="{'active':index==selected}">{{item.classify}}</a>
      </li>
    </ul>
      <article v-for='item in articleLists' v-if='show' :key="item._id" >
        <header>
          <div>
            <router-link :to="{path:`/article/${item._id}`}" class="tags_title" >
              {{item.title}}
            </router-link>
        </div>
        <div>
          <p class="tags_creatAt">{{item.created_at}}</p>
        </div>
      </header>
      <footer>
        <router-link class="tags_readMore" :to="{path:`/article/${item._id}`}">阅读全文>></router-link>
      </footer>
    </article>
  </div>
</template>

<script>
import api from '../../api'
import vfoot from './vfooter'
export default {
  name:"Tag",
  data(){
    return {
      items:[],
      articleLists:[],
      selected:9,
      show:true,
      loading2:true,
      list_show:true
    }
  },
  components:{
    vfoot
  },
  methods:{
    gets(index,classify){
      this.selected = index
      this.loading2 = true
      this.getPage(classify)
    },
    getPage(classify){
      api.getArticlesByClassify({classify})
        .then((result)=>{
          setTimeout(()=>{
            this.articleLists = result.data.articleLists
            this.loading2 = false
          },500) 
        })
    }
  },
  mounted(){
      // 根据标签名获取文章列表
    api.getClassify()
      .then(({data:{code,lists}})=>{
        if(code==200){
          setTimeout(()=>{
            this.loading2=false;
            this.items = lists
            // this.getPage('Vue')
          },500)
        }
      })
      .catch(err=>{
        alert(err.message);
      })
  }
}
</script>

<style lang="scss" scoped>
@import '../../scss/media-queries';
@import '../../scss/variables';
.tags{
  
  &_list{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    li{
      margin:.4rem;
      a{
        display: block;
        padding: .6rem 1.6rem;
        border:1px solid #d2d2d2;
        border-radius: .6rem;
        color:rgba(0, 0, 0, .8);
        font-size: 1.8rem;
        /*transition渐变*/
        background-color: #f7f7f7;
        transition: all .4s;
        cursor: pointer;
      }
      .active{
        background-color: #555555;
        color: #fff;
      }
    }
  }
  &_wrap{
    margin:auto;
    list-style: none;
    margin-top: 20px;
    article{
      padding-bottom: 1rem;
      border-bottom:1px solid #d2d2d2;
    }
  }
  &_title{
		display: block;
		font-size: 2.5rem;
		font-weight: 400;
		color: #404040;
		padding: 32px 32px;
		margin: 0;
	}
	&_creatAt{
		font-family: "Comic Sans MS", curslve, sans-serif;
		font-size: 1.6rem;
		color:#7f8c8d;
		margin: 0;
		padding: 0 32px;
	}	
	&_readMore{
		font-size: 2rem;
    color:#919191;
    font-weight: 600;
	}
}
article{
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 6px, rgba(0, 0, 0, 0.2) 0px 1px 4px;
  border-radius: 2px;
  background-color: rgba($c-white, 0.8);
  box-sizing: border-box;
  transition: all 0.5s ease;
  display: flex;
  flex-direction: column;
  margin: 16px 0;
  footer {
    padding: 32px 32px;
  }
  &:hover{
    box-shadow: rgba(0, 0, 0, 0.322) 0px 2px 12px, rgba(0, 0, 0, 0.459) 0px 2px 8px;
  }
}
h2,h4{
  margin:0;
}
p,.tags_main p{
  margin:0;
}
footer{
  text-align: right;
}
.list-enter-active,.list-leave-active{
  transition: all .3s
}
.list-enter,.list-leave-active{
  opacity: 0;
}
</style>



