<template>
	<div class="header-show">
		<div class="header_curtain-black" v-show="show" @click="show = !show"></div>
		<div class="header_toggle" @click="show = !show">
			<div class="bar"></div>
			<div class="bar"></div>
			<div class="bar"></div>
		</div>
		<!-- <transition name="slide"> -->
			<header class="header"  :class="{ 'active' : show }">
				<div class="header_bg" >
					<nav class="header_nav">
						<div class="header_top">
							<img src="http://p3v3pwvel.bkt.clouddn.com/avator.jpg" class="header_top--avator"/>
							<div class="header_top--shortT">爱音乐,爱生活</div>
							<div class="header_top--shortT">xu1092269834@gmail.com</div>
						</div>
						<ul class="header_list">
							<li class="header_item"><router-link to='/home'>首页</router-link></li>
							<li class="header_item"><router-link to="/tags">标签</router-link></li>
							<li class="header_item"><router-link to="/about">关于</router-link></li>
						</ul>
					</nav>
				</div>
			</header>
		<!-- </transition> -->
	</div>
  
</template>

<script>
export default {
	data() {
		return {
			show: false,
		}
	},
	methods:{
		toggle(){
			this.show = !this.show
		}
	}
}
</script>

<style scoped lang="scss">
@import '../../scss/media-queries';
@import '../../scss/variables';
.header{
	width: 100%;
	color: $c-dark;
	z-index: 10;
	position: fixed;
	top: 0;
	left: -27rem;
	width: 60%;
	height: 100%;
	transition:  all .2s ease;
	@include tablet-min{
		width: 27rem;
	}
	&_show{
		width: 100%;
	}
	&_curtain-black{
		width: 100%;
		height: 100%;
		background-color: rgba($c-dark, 0.6);
		position: fixed;
		top: 0;
		left: 0;
		z-index: 8;
	}
	&_toggle{
		display: flex;
		flex-direction: column;
		margin: 2px 2px;
		width: 40px;
		height: 40px;
		position: relative;
		left: 0;
		top: 0;
		cursor: pointer;
		display: inline;
		@include tablet-min{
			position: fixed;
		}
		.bar{
			position: absolute;
			width: 22px;
			height: 2px;
			background: rgba($c-white, 1);
			&:nth-child(1){
				left: 11px;
				top: 12px;
				transition: all 0.5s ease;
			}
			&:nth-child(2){
				left: 11px;
				top: 20px;
				transition: all 0.5s ease;
			}
			&:nth-child(3){
				left: 11px;
				top: 28px;
				transition: all 0.5s ease;
			}
		}
	}
	&_bg{
		background:  url('http://p3v3pwvel.bkt.clouddn.com/bg5.jpg') no-repeat center /cover;
		filter: brightness(0.9);
		text-align: center;
		height: 240px;
		width: 100%;
		z-index: 90;
		height: 100%;
		width: 100%;
		left: 0;
		top: 0;
		text-align: center;
	}
	&_top{
		width: 100%;
		height: 60%;
		&--avator{
			width: 105px;
			height: 105px;
			border-radius: 50%;
			margin: 10px 0;
			@include tablet-min{
				width: 80px;
				height: 80px;
				margin: 20px 0;
			}
		}
		&--shortT{
			padding: 5px 0;
		}
	}
	&_list{
		display: flex;
		margin:0;
		flex-direction: column;
		justify-content: center;
		list-style: none;
		align-items: flex-end;
		justify-content: center;
	}
	&_item{
		a{
			&:hover{
				opacity: 0.7;
			}
			font-size: 18px;
			font-weight: 500;
			display: block;
			padding: 1.2rem 1.8rem;
			color: $c-dark;
			opacity: 1;
			transition: opacity 0.5s;
		}
	}
}
.active{
	left: 0;
}
.slide-enter-active,.move-leave-active {
  transition: all .3s ease;
}
.slide-enter{
  transform: translateX(-320px);
  opacity: 0;
}
.slide-leave-to{
	transform: translateX(320px);
}
// .slide-enter{
// 	transform: translateX(-320px);
// }
// .slide-leave-to {
//   transform: translateX(-320px);
// }
</style>



