<template>
<transition name="slide-fade">
  <article v-loading="loading2"  element-loading-text="加载中" class="article">
    <header>
      <div class="article_title">{{oneArticle.title}}</div>
      <div>
        <p class="article_creatAt">{{oneArticle.created_at}}</p>
      </div>
    </header>
    <section v-html="oneArticle.contentToMark" class="article_main"></section>
    <a href="javascript:;" class="article_reback" onclick="javascript:history.back(-1)">{{returnMsg}}</a>
  </article>
</transition>
</template>
<script>
import api from '../../api'
export default {
  data(){
    return {
      oneArticle:{},
      loading2:false,
      returnMsg:'返回上一页'
    }
  },
  created(){
      api.getOneArticle({id:this.$route.params.id})
        .then((result)=>{
            this.oneArticle = result.data.articleInfo
        })
  }
}
</script>

<style lang="scss" scoped>
@import '../../scss/media-queries';
@import '../../scss/variables';
.article{
  margin: 0 auto;
  width: 100%;
  background-color: rgba($c-white, 0.8);
  padding: 0;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 6px, rgba(0, 0, 0, 0.2) 0px 1px 4px;
	border-radius: 2px;
  box-sizing: border-box;
  margin-top: 32px;
  @include tablet-min{
    margin-top: 64px;
    width: 90%;
  }
  &_title{
    display: block;
		font-size: 2.5rem;
		font-weight: 400;
		color: #404040;
		padding: 18px 32px;
		margin: 0;
  }
  &_creatAt{
    font-family: "Comic Sans MS", curslve, sans-serif;
		font-size: 1.6rem;
		color:#7f8c8d;
		margin: 0;
		padding: 0 32px;
  }
  &_main{
    font-size: 1.6rem;
    line-height: 1.6em;
    padding: 16px 32px;
  }
  &_reback{
    padding: 32px;
    display: block;
  }
}
.slide-fade-enter-active {
transition: all .2s ease;
}
.slide-fade-leave-active {
transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to {
transform: translateX(10px);
opacity: 0;
}
</style>



