<template>
    <footer>
        <p>{{footMsg}}</p>
    </footer>
</template>

<script>
import info from '../../another.json';
export default {
  data() {
    return {
      footMsg: info.footMsg
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../scss/media-queries';
@import '../../scss/variables';
  footer{
    height: 10rem;
    text-align: center;
    display: flex;
    display: -webkit-flex;
  }
  footer p{
    margin:auto;
    font-size: 1.4rem;
    color: #3d3d3d;
    padding: 1rem 1.5rem;
  }
  @media screen and (max-width:768px){
    footer{
      height: 6rem;
    }
  }
</style>



